/**
 * replyItem 评论的回复组件
 * @description: 单个回复组件
 * @Author: 穆兰
 * @Date: 2022/1/5
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/7
 * @property {Object} [replyCommentData] - 回复的数据
 * @property {Boolean} [isAuthor] - 是否为动态的作者
 * @property {Boolean} [isLead] - 是是否为评论的作者
 * @event {Function} onClick - 点击回复主体
 * @event {Function} onMoreClick - 点击更多按钮
 */
export default {
    name: "reply-item",
    props: {
        replyCommentData: {
            type: Object,
            required: true
        },
        isAuthor: {
            type: Boolean,
            default: false
        },
        isLead: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {};
    },
    methods: {
        /**
         * @description  点击回复主体
         */
        onClick() {
            if (this.replyCommentData.isDeleted != 1) {
                this.$emit("onClick");
            }
        },
        /**
         *@description 点击更多按钮
         */
        onMoreClick() {
            this.$emit("onMoreClick");
        }
    }
};
